* {
  box-sizing: border-box;
  -ms-overflow-style: none;
  scrollbar-width: none;
  margin: 0;
  padding: 0;
}

::-webkit-scrollbar {
  display: none;
}

.page {
  scroll-behavior: smooth;
  font-family: Poppins, sans-serif;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 28px;
}

.page__body {
  min-width: 320px;
}

.page__body:has(.page__menu:target) {
  overflow: hidden;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  z-index: 2;
}

.header {
  color: #fff;
  background-color: #2c2c2c;
  border-radius: 0 0 30px 30px;
  flex-direction: column;
  height: 893px;
  display: flex;
}

@media (width >= 1024px) {
  .header {
    height: 800px;
  }
}

.header:before {
  content: "";
  z-index: -1;
  background-color: #f2f6fa;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.header__content {
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-between;
  margin-top: 28px;
